<template>
  <b-modal
    id="modal-add-delivery"
    no-close-on-backdrop
    no-fade
    size="lg"
    ref="modal-add-delivery"
    hide-footer
    title="Danh sách hợp đồng"
  >
    <div class="row mb-2">
      <div class="col-12">
        <div class="input-group">
          <input
            type="search"
            placeholder="Nhập mã số thuế hoặc tên doanh nghiệp để tìm kiếm...."
            class="form-control"
            v-model="keyword"
            @keyup.enter="search()"
          />
          <button
            @click="search()"
            type="button"
            id="button-addon2"
            class="btn btn-primary-soft"
          >
            <i class="fa-solid fa-magnifying-glass"></i> Tìm kiếm
          </button>
        </div>
      </div>
    </div>

    <div class="card border-0">
      <div class="card-body p-0 overflow-auto" style="height: 400px">
        <table class="table">
          <thead>
            <tr>
              <th>#</th>
              <th width="55%">Tên công ty</th>
              <th>Mã số thuế</th>
              <th>Ngày HĐ</th>
              <th>Tổng tiền (VAT)</th>
              <th width="10"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item,index) in contracts" :key="item.contract_id">
              <td>{{ index+1 }}</td>
              <td><span class="text-primary">{{ item.company_name }}</span></td>
              <td>{{ item.company_taxcode }}</td>
              <td>{{ item.contract_date | moment('DD/MM/YYYY') }}</td>
              <td>
                <strong class="text-danger">
                  {{ item.contract_total | formatamout }}</strong
                >
              </td>
              <td>
                <button class="btn btn-primary-soft btn-sm" @click="sendItem(item)">
                  <i class="fa-light fa-circle-arrow-right"></i>
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </b-modal>
</template>

<script>
export default {
  data() {
    return {
      keyword: "",
      contracts: [],
    };
  },
  methods: {
    search() {
      this.getContracts(this.searchArray);
    },
    show() {
      this.$bvModal.show("modal-add-delivery");
      this.getContracts(this.searchArray);
    },
    sendItem(item) {
      this.$emit("contract", item);
      this.$bvModal.hide("modal-add-delivery");
    },
    async getContracts(searchArray) {
      let startTime = performance.now();
      this.$econtract.showLoading();
      await axios
        .post(`contracts`, {
          name: this.keyword,
        })
        .then((result) => {
          if (result.status == 200) {
            this.contracts = result.data.data;
          }
          this.$econtract.hideLoading();
          console.log("Time to started " + (performance.now() - startTime));
        })
        .catch((err) => {});
    },
  },
};
</script>
