<template>
  <div id="delivery">
    <div class="row">
      <div class="col-10 offset-1">
        <!-- {{ Delivery.delivery_date }} -->
        <h1 class="title-page fw-bold">{{ title }}</h1>
        <div class="card border-0">
          <div class="card-body p-0">
            <ul class="list-group">
              <li class="list-group-item bg-light">
                <strong>Thông tin hợp đồng</strong>
              </li>
              <li class="list-group-item">
                <div class="row">
                  <div class="col-6">
                    <div class="form-group">
                      <label for="">Hợp đồng</label>
                      <div class="input-group">
                        <input
                          type="text"
                          class="form-control"
                          placeholder="Chọn hợp đồng"
                          v-model="Delivery.company_name"
                        />

                        <button
                          class="btn btn-outline-secondary"
                          type="button"
                          id="button-addon3"
                          @click="showContracts()"
                        >
                          <i class="fa-thin fa-magnifying-glass"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                  <div class="col-3">
                    <div class="form-group">
                      <label for="">Mã số thuế</label>
                      <input
                        type="text"
                        class="form-control"
                        v-model="Delivery.company_taxcode"
                        placeholder="Mã số thuế"
                      />
                    </div>
                  </div>
                  <div class="col-3">
                    <div class="form-group">
                      <label for="">Ngày biên bản</label>
                      <date-picker
                        v-model.lazy="Delivery.delivery_date"
                        :config="{
                          format: 'DD/MM/YYYY',
                          useCurrent: false,
                        }"
                      ></date-picker>
                    </div>
                  </div>
                </div>

                <div class="row">
                  <div class="col-6">
                    <div class="form-group">
                      <label for="">Ngày bắt đầu</label>
                      <date-picker
                        v-model.lazy="Delivery.contract_date_start"
                        :config="options"
                      ></date-picker>
                    </div>
                  </div>
                  <div class="col-6">
                    <div class="form-group">
                      <label for="">Ngày kết thúc</label>
                      <date-picker
                        v-model.lazy="Delivery.contract_date_end"
                        :config="options"
                      ></date-picker>
                    </div>
                  </div>
                </div>
              </li>
              <li class="list-group-item bg-light">
                <strong>Thông tin thuê bao</strong>
              </li>
              <li class="list-group-item">
                <div class="row">
                  <div class="col-6">
                    <div class="form-group">
                      <label for="">Tên thuê bao</label>
                      <input
                        type="text"
                        v-model="Delivery.ten_tb"
                        class="form-control"
                        placeholder="Tên thuê bao"
                      />
                    </div>
                  </div>

                  <div class="col-6">
                    <div class="form-group">
                      <label for="">Số HĐ & Ngày HĐ</label>
                      <input
                        type="text"
                        v-model="Delivery.contract_number_content"
                        class="form-control"
                        placeholder="Số HĐ & Ngày HĐ"
                      />
                    </div>
                  </div>
                </div>

                <div class="row">
                  <div class="col-12">
                    <div class="form-group">
                      <label for="">Serial Number</label>
                      <input
                        type="text"
                        class="form-control"
                        v-model="Delivery.serial_number"
                        placeholder="Serial Number"
                        required
                      />
                    </div>
                  </div>
                </div>
              </li>

              <li class="list-group-item bg-light fw-bold">
                Thông tin nhân viên bàn giao
              </li>
              <li class="list-group-item">
                <div class="row">
                  <div class="col-6">
                    <div class="form-group">
                      <label for="">Nhân viên bàn giao</label>
                      <input
                        type="text"
                        class="form-control"
                        v-model="Delivery.staff_name"
                        placeholder="Nhân viên bàn giao"
                      />
                    </div>
                  </div>

                  <div class="col-6">
                    <div class="form-group">
                      <label for="">Chức vụ</label>
                      <input
                        type="text"
                        class="form-control"
                        v-model="Delivery.staff_position"
                        placeholder="Nhân viên bàn giao"
                      />
                    </div>
                  </div>
                </div>
              </li>
              <li class="list-group-item bg-light fw-bold">
                Thông tin bên nhận bàn giao
              </li>
              <li class="list-group-item">
                <div class="row">
                  <div class="col-6">
                    <div class="form-group">
                      <label for="">Bên nhận bàn giao</label>
                      <input
                        type="text"
                        class="form-control"
                        v-model="Delivery.recipient_name"
                        placeholder="Bên nhận bàn giao"
                      />
                    </div>
                  </div>
                  <div class="col-6">
                    <div class="form-group">
                      <label for="">Chức vụ bên nhận</label>
                      <input
                        type="text"
                        class="form-control"
                        v-model="Delivery.recipient_position"
                        placeholder="Chức vụ"
                      />
                    </div>
                  </div>
                </div>
              </li>
              <li class="list-group-item bg-light fw-bold">Thông tin thay đổi</li>
              <li class="list-group-item">
                <div class="row">
                  <div class="col-6">
                    <div class="form-group">
                      <label for="">Loại</label>
                      <select class="form-select" v-model="Delivery.delivery_type">
                        <option value="0">BBBG</option>
                        <option value="1">Thay đổi thông tin</option>
                        <option value="2">Thay đổi thiết bị</option>
                      </select>
                    </div>
                  </div>

                  <div class="col-6">
                    <div class="form-group">
                      <label for="">Tên công cũ</label>
                      <input
                        type="text"
                        class="form-control"
                        v-model="Delivery.company_name_old"
                        placeholder="Tên công ty cũ"
                      />
                    </div>
                  </div>
                </div>

                <div class="row">
                  <div class="col-6">
                    <div class="form-group">
                      <label for="">Tên công mới</label>
                      <input
                        type="text"
                        class="form-control"
                        v-model="Delivery.company_name_new"
                        placeholder="Tên công ty mới"
                      />
                    </div>
                  </div>
                </div>

                <div class="row">
                  <div class="col-12">
                    <div class="form-check form-switch">
                      <input
                        v-model="isEcontract"
                        class="form-check-input"
                        type="checkbox"
                        role="switch"
                        id="flexSwitchCheckDefault"
                      />
                      <label class="form-check-label" for="flexSwitchCheckDefault"
                        >Hợp đồng điện tử</label
                      >
                    </div>
                  </div>
                </div>
              </li>
            </ul>
          </div>
          <div class="card-footer">
            <button class="btn btn-primary me-2" @click="store()">
              <i class="fa-thin fa-floppy-disk"></i> Lưu
            </button>
            <router-link class="btn btn-secondary" :to="{ name: 'delivery-index' }">
              <i class="fa-thin fa-arrow-left"></i> Quay lại
            </router-link>
          </div>
        </div>
      </div>
    </div>
    <ModalContracts ref="modalcontract" @contract="getContract"></ModalContracts>
  </div>
</template>

<script>
import datePicker from "vue-bootstrap-datetimepicker";
import "pc-bootstrap4-datetimepicker/build/css/bootstrap-datetimepicker.css";
import ModalContracts from "@/extends/ModalContracts.vue";
import axios from "axios";
export default {
  components: {
    datePicker,
    ModalContracts,
  },
  data() {
    return {
      title: "Tạo mới BBBG",
      Delivery: {
        company_name: "",
        delivery_type: 0,
        delivery_id: 0,
        contract_id: 0,
        delivery_date: this.$econtract.formatDate(new Date()),
        staff_name: "Nguyễn Hữu Lễ",
        staff_position: "Nhân viên",
        serial_number: "",
        recipient_name: "",
        recipient_position: "",
        company_name_new: "",
        company_name_old: "",
        company_taxcode: "",
        contract_date_start: this.$econtract.formatDate(new Date()),
        contract_date_end: this.$econtract.formatDate(new Date()),
        ten_tb: "",
      },
      options: {
        format: "DD/MM/YYYY",
        useCurrent: false,
      },
      isEcontract: false,
      contract: {},
      isUpdate: false,
    };
  },
  watch: {
    isEcontract: {
      handler(newValue) {
        if (newValue) {
          this.Delivery.staff_name = "Nguyễn Thị Diễm Mi";
          this.Delivery.staff_position = "PGĐ Phòng KH-TC-DN";
        } else {
          this.Delivery.staff_name = "Nguyễn Hữu Lễ";
          this.Delivery.staff_position = "Nhân viên";
        }
      },
    },
  },
  created() {
    if (this.$route.params.id > 0) {
      this.isUpdate = true;
      this.title = "Cập nhật biên bản bàn giao";
      this.show(this.$route.params.id);
    }
  },
  methods: {
    async store() {
      if (!this.Delivery.serial_number) this.$toast.warning("Bạn chưa nhập số Serial");
      else {
        if (!this.isUpdate) {
          await axios.post("deliverys", this.Delivery).then((response) => {
            if (response.status == 200) {
              this.$toast.success("Thêm biên bản thành công");
              this.$router.push({
                name: "delivery-index",
              });
            }
          });
        } else {
          await axios.put("deliverys/update", this.Delivery).then((response) => {
            if (response.status == 200) {
              this.$toast.success("Cập nhật biên bản thành công");
              this.$router.push({
                name: "delivery-index",
              });
            }
          });
        }
      }
    },
    showContracts() {
      this.$refs.modalcontract.show();
    },
    async show(id) {
      await axios.get(`deliverys/${id}`).then((response) => {
        this.Delivery = response.data;

        this.Delivery.delivery_date = this.$econtract.formatDate(
          response.data.delivery_date
        );

        this.Delivery.contract_date_start = this.$econtract.formatDate(
          response.data.contract_date_start
        );

        this.Delivery.contract_date_end = this.$econtract.formatDate(
          response.data.contract_date_end
        );
      });
    },
    getContract(item) {
      this.Delivery.contract_id = item.contract_id;
      this.Delivery.company_name = item.company_name;
      this.Delivery.company_taxcode = item.company_taxcode;
      this.Delivery.ten_tb = item.company_name;
      // this.Delivery.delivery_date = this.$econtract.formatDate(item.contract_date_start);
      this.Delivery.recipient_name = item.company_owner;
      this.Delivery.recipient_position = item.company_position;
      this.Delivery.contract_date_start = this.$econtract.formatDate(
        item.contract_date_start
      );
      this.Delivery.contract_date_end = this.$econtract.formatDate(
        item.contract_date_end
      );
    },
  },
};
</script>
